export default {
	data() {
		return {
			title: '你好我是第一个界面',
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
		}
	},
	onLoad() {

	},
	onShow(){
		this.saveStroage();
	},
	
	methods: {
		linkRouter(){
			uni.navigateTo({
			    url: '../wordList/wordList'
			});
		},
		linkPage(){
			uni.navigateTo({
			    url: '../pagination/pagination'
			});
		},
		linkForm(){
			uni.navigateTo({
			    url: '../uniForm/uniForm'
			});
		},
		linkPicker(){
			uni.navigateTo({
			    url: '../picker/picker'
			});
		},
		saveStroage(){
			let nameList1 = [];let nameObj = {};
			for(let i=0;i<10000;i++){
				nameObj = {};
				nameObj.id = i;
				nameObj.name = '王' + i;
				nameList1.push(nameObj);
			};
			let nameList = [];
			nameList = JSON.parse(JSON.stringify(nameList1))
			uni.setStorage({
			    key: 'nameList',
			    data: nameList,
			    success: function () {
			        console.log('success');
			    }
			});
		}
	}
}
